.o-input, .o-select, .o-textarea {
  height: 36px;
  border: 1px solid #DADADA;
  color: #484848;
  background-color: #FFF;
  border-radius: 3px;
  padding: 7px 8px;
  display: block;
  width: 100%;
  line-height: 20px;
}

.o-input[disabled], [disabled].o-select, [disabled].o-textarea {
  opacity: 0.75;
  background-color: #f5f5f5;
}

.o-input[readonly], [readonly].o-select, [readonly].o-textarea {
  background-color: rgba(245, 245, 245, 0.5);
}

.o-input::placeholder, .o-select::placeholder, .o-textarea::placeholder {
  color: inherit;
  opacity: 0.3;
}

.o-input--xl, .o-textarea--xl {
  height: 54px;
  font-size: 1.2em;
  padding: 16px 14px;
}

.o-input--l, .o-textarea--l {
  height: 44px;
  font-size: 1.1em;
  padding: 11px 10px;
}

.o-input--s, .o-textarea--s {
  height: 28px;
  font-size: 0.9em;
  padding: 3px 6px;
}

.o-input--xs, .o-textarea--xs {
  height: 22px;
  line-height: 18px;
  font-size: 0.8em;
  padding: 1px 4px;
}

.o-select {
  background-image: url(../img/select-trigger.svg);
  background-position: right center;
  background-repeat: no-repeat;
  padding: 7px 34px 7px 8px;
}

.o-select option {
  padding: 5px 11px;
}

.o-select--xl {
  height: 54px;
  font-size: 1.2em;
  padding: 14px 34px 14px 14px;
}

.o-select--xl option {
  padding: 5px 17px;
}

.o-select--l {
  height: 44px;
  font-size: 1.1em;
  padding: 10px 34px 10px 10px;
}

.o-select--l option {
  padding: 5px 13px;
}

.o-select--s {
  height: 28px;
  font-size: 0.9em;
  padding: 3px 32px 3px 6px;
}

.o-select--s option {
  padding: 2px 9px;
}

.o-select--xs {
  height: 22px;
  line-height: 18px;
  font-size: 0.8em;
  padding: 2px 32px 1px 4px;
}

.o-select--xs option {
  padding: 1px 5px;
}

.o-select--multiple {
  height: auto;
  padding: 0;
  background-image: none;
}

.o-select--multiple option {
  padding: 8px 12px 8px 10px;
}

.o-select--multiple.o-select--xl option {
  padding: 15px 14px 15px 14px;
}

.o-select--multiple.o-select--l option {
  padding: 11px 14px 11px 14px;
}

.o-select--multiple.o-select--s option {
  padding: 5px 10px 5px 6px;
}

.o-select--multiple.o-select--xs option {
  padding: 3px 6px 3px 4px;
}

.o-textarea {
  line-height: 1.2;
  padding-top: 7px;
  padding-bottom: 7px;
}

.o-textarea--xl {
  padding-top: 14px;
  padding-bottom: 14px;
}

.o-textarea--l {
  padding-top: 11px;
  padding-bottom: 11px;
}

.o-textarea--s {
  padding-top: 5px;
  padding-bottom: 5px;
}

.o-textarea--xs {
  padding-top: 3px;
  padding-bottom: 3px;
}

.o-textarea {
  height: auto;
}

body {
  background: #fff;
}

.loader {
  border: 2px solid red;
  border-top-color: transparent;
  border-right-color: transparent;
  border-radius: 50%;
  animation: spinAround 500ms infinite linear;
}

.collapse {
  display: none;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  border-top-width: 0 !important;
  border-bottom-width: 0 !important;
}

.collapse.in {
  display: block;
}

.is-transitioning,
.collapsing {
  position: relative;
  overflow: hidden;
  transition: height .3s ease;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  border-top-width: 0 !important;
  border-bottom-width: 0 !important;
}

button {
  height: 40px;
  padding: 10px;
}

body {
  padding: 20px;
}

body::after {
  font: 13px/18px Arial;
  text-align: center;
  content: 'desktop';
  position: fixed;
  right: 0;
  bottom: 0;
  padding: 5px 20px;
  min-width: 100px;
  z-index: 9999;
  background: rgba(0, 120, 0, 0.5);
  color: #fff;
}

@media (min-width: 600px) and (max-width: 959px) {
  body::after {
    content: 'tablet';
    background: rgba(0, 0, 255, 0.5);
  }
}

@media (max-width: 599px) {
  body::after {
    content: 'mobile';
    background: rgba(255, 0, 0, 0.5);
  }
}

.flex {
  display: flex;
